#loginPage {
  height: 100vh;
  width: 100vw;
  *,
  *:before,
  *:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  background-color: #080710;
  .background {
    width: 430px;
    height: 520px;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
  }
  .background .shape {
    height: 200px;
    width: 200px;
    position: absolute;
    border-radius: 50%;
  }
  .shape:first-child {
    background: linear-gradient(#1845ad, #23a2f6);
    left: -80px;
    top: -80px;
  }
  .shape:last-child {
    background: linear-gradient(to right, #ff512f, #f09819);
    right: -30px;
    bottom: -80px;
  }
  form {
    height: 520px;
    width: 400px;
    background-color: rgba(255, 255, 255, 0.13);
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 50px 35px;
  }
  form * {
    font-family: "Poppins", sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
  }
  form h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
  }

  label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
  }
  input {
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
  }
  ::placeholder {
    color: #e5e5e5;
  }
  button {
    margin-top: 50px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
  }
  .social {
    margin-top: 30px;
    display: flex;
  }
  .social div {
    background: red;
    width: 150px;
    border-radius: 3px;
    padding: 5px 10px 10px 5px;
    background-color: rgba(255, 255, 255, 0.27);
    color: #eaf0fb;
    text-align: center;
  }
  .social div:hover {
    background-color: rgba(255, 255, 255, 0.47);
  }
  .social .fb {
    margin-left: 25px;
  }
  .social i {
    margin-right: 4px;
  }
}

#homePage {
  width: 100vw;
  height: 100vh;
  * {
    margin: 0;
    padding: 0;
    font-family: Trebuchet MS;
  }
  .wrapper {
    width: 750px;
    position: relative;
    top: 20px;
    height: 500px;
    left: 5rem;
    margin-top: 20px;
  }
  .wrapper > div.main-container {
    z-index: 1;
    position: absolute;
    display: inline-block;
    padding: 10px 10px;
    width: 400px;
    height: 100%;
    background: #f1f0f0;
    border-radius: 4px;
    box-shadow: inset 0px 0px 18px -5px rgba(0, 0, 0, 0.75);
    margin-right: -5px;
  }

  .chat {
    display: flex;
  }

  .message-area {
    display: flex;
    flex-direction: column-reverse;
    overflow-y: scroll;
    height: 100px;
    resize: none; /* Remove this if you want the user to resize the textarea */
  }

  div.main-container > div.message-area {
    margin-bottom: 10px;
    width: 100%;
    height: 440px;
    margin-top: 5px;
    background: white;
  }
  div.main-container > div.enter-area {
    width: 100%;
    height: 40px;
  }
  div.main-container > div.enter-area > form {
    width: 100%;
    height: 100%;
  }
  div.main-container > div.enter-area > form > input#message-ent {
    font-size: 15px;
    width: 300px;
    height: 90%;
    padding-left: 10px;
  }
  div.main-container > div.enter-area > form > input#message-send {
    border: none; /*remove styling */
    font-size: 15px;
    width: 20%;
    height: 100%;
    float: right;
    background-color: white;
    color: #00b7d1;
  }

  .online-tab {
    position: relative;
    top: 30px;
    display: inline-block;
    width: 250px;
    height: auto;
    margin: 0 auto;
    background: black;
    border-radius: 5px;
    background-color: #8bc4cc;
    padding: 10px;
    padding-bottom: 15px;
    box-shadow: inset 20px -9px 82px -49px rgba(0, 0, 0, 0.75);
  }

  .status-bar-user {
    line-height: 40px;
    font-size: 20px;
    margin-top: 5px;
    width: 100%;
    height: 40px;
    background-color: white;
  }
  .status-bar-user > p {
    position: relative;
    left: 10px;
  }

  .status-bar-user > p > span {
    color: green;
  }

  .online-user-list {
    margin-top: 5px;
    width: 100%;
    height: 380px;
    background-color: white;
  }

  .online-tab-open {
    position: absolute;
    background-color: white;
    font-size: 30px;
    width: 40px;
    height: 40px;
    top: 10px;
    right: -45px;
    line-height: 40px;
    border-radius: 3px;
    border: 3px double #2eb9ce;
    text-align: center;
    vertical-align: middle;
    user-select: none;
    cursor: pointer;
  }
  .online-tab-open:active {
    border: 3px solid #2eb9ce;
  }

  .welcome-banner {
    position: relative;
    top: 0;
    width: 400px;
    height: 50px;
    background-color: #54a9b4;
    left: -400px;
    transform-origin: top;
    animation: welcome-slidein-one 2s forwards,
      welcome-slidein-two 0.5s forwards, welcome-slidein-three 0.3s forwards;
    animation-delay: 2s, 4s, 4.3s;
  }

  .welcome-banner:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border-style: solid;
    border-width: 50px 50px 0px 0px;
    border-color: #54a9b4 white;
  }

  .welcome-banner > .name-side {
    line-height: 50px;
    font-size: 20px;
    width: 100%;
    height: 100%;
  }
  .welcome-banner > .name-side > p {
    padding-left: 20px;
    color: white;
  }

  @keyframes welcome-slidein-one {
    0% {
      left: -400px;
    }
    100% {
      left: 200px;
    }
  }
  @keyframes welcome-slidein-two {
    0% {
      left: 200px;
    }
    100% {
      left: 180px;
    }
  }
  @keyframes welcome-slidein-three {
    0% {
      left: 180px;
      opacity: 1;
    }
    100% {
      left: 800px;
      opacity: 0;
    }
  }
  /* Toggle Classes */
  .online-tab-toggle {
    left: 420px;
  }

  .submitComment {
    cursor: pointer;
  }

  .message-blue {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #a8ddfd;
    width: 200px;
    min-height: 50px;
    text-align: left;
    font: 400 0.9em "Open Sans", sans-serif;
    border: 1px solid #97c6e3;
    border-radius: 10px;
    margin-left: 2rem;
    margin-top: 0.5rem;
  }

  .message-blue:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-top: 15px solid #a8ddfd;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    top: 0;
    left: -15px;
  }

  .message-blue:before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-top: 17px solid #97c6e3;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    top: -1px;
    left: -17px;
  }

  .message-timestamp-right {
    // position: absolute;
    font-size: 0.85em;
    font-weight: 300;
    margin-top: 0.5em;
    // bottom: 5px;
    // right: 5px;
  }

  .message-timestamp-left {
    // position: absolute;
    font-size: 0.85em;
    font-weight: 300;
    margin-top: 0.5em;
    // bottom: 5px;
    // left: 5px;
  }

  .message-orange {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10px;
    margin-left: calc(100% - 240px);
    padding: 10px;
    background-color: #f8e896;
    width: 200px;
    min-height: 50px;
    text-align: left;
    font: 400 0.9em "Open Sans", sans-serif;
    border: 1px solid #dfd087;
    border-radius: 10px;
  }

  .message-orange:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 15px solid #f8e896;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    bottom: 0;
    right: -15px;
  }

  .message-orange:before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 17px solid #dfd087;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    bottom: -1px;
    right: -17px;
  }

  .onlineUser {
    font-weight: 800;
    color: green;
  }
}
